:root{
  --primary: #d81e5d;
  --secondary: #8c388c;
  --dark: #131a26;
  --light:#eee;

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;

}

body{
  background-color: var(--light);
}

.App{
  padding: 16px;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;

}

.Calculator{
  width: 100%;
  max-width: 400px;
  background-color: aliceblue;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.Display{
  padding: 16px;
  text-align: right;
  background-color: var(--dark);
  color:var(--light);
  font-size: 24px;
  font-weight: 300;
}

.Display span{
  font-size: 14px;
  color: #888;

}

button{
appearance: none;
border: none;
outline: none;
color: #FFF;
font-size: 20px;
padding:16px;
cursor: pointer;
transition: 0.4s;
}

button :hover{
  opacity: 0.9;
}

.Operators{
  display: flex;

}

.Operators button{
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
}

.Digits{
  display: flex;
  flex-wrap: wrap;

}

.Digits button{
  flex: 1 1 33.3333%;
  max-width: 33.333%;
  background-color: var(--dark);


}